import React from 'react'
import '../Styles/App.css'

class ToDoList extends React.Component {
    constructor() {
        super();
        this.state = {
            todo: '',
            time: '',
            notes: [],
        }
    }

    //On Handling Input Boxes
    handleInput = (e) => {
        let name = e.target.name;
        let value = e.target.value;

        this.setState({
            [name]: value
        })
    }


    //Onclicking Submit Button
    submitNote = () => {
        let newNote = {
            todo: this.state.todo,
            estTime: this.state.time,
            startTime: 0,
            endTime: 0
        }
        this.setState({
            notes : [...this.state.notes, newNote]
        })
    }

    //Getting current time in milliseconds
    getTimeMilli = () => {
        var d = new Date();
        return d.getTime();
    }

    //Getting Current time in format HH:MM:SS
    getFormatTime = (timiInMilliseconds) => {
        return new Date(timiInMilliseconds).toISOString().substr(11,8)
    }

    //Onclick Of starting a ToDO
    start = (e) => {
        let time = this.getTimeMilli();
        this.setState({
            notes: this.state.notes.map((el, i) => (i == e.target.name ? Object.assign({}, el, { startTime: time }) : el))
        })
    }

    //Onclick of ending the ToDO
    end = (e) => {
        let time = this.getTimeMilli();
        if (this.state.notes[e.target.name].startTime != 0) {
            this.setState({
                notes: this.state.notes.map((el, i) => (i == e.target.name ? Object.assign({}, el, { endTime: time }) : el))
            })
        }
        else {
            alert("Please Start the task first")
        }
    }


    render() {
        const todo = this.state.notes.map((element, index) => {
            return (
                <tr key={index}>
                    
                    <td style={{ textAlign: "center" }}>{element.todo}</td>

                    <td style={{ textAlign: "center" }}>{element.estTime}</td>

                    {
                    this.state.notes[index].startTime === 0 ? 
                    <td style={{ textAlign: "center" }}>
                         <div style={{ width: "100%" }}>
                            <button name={index} className="btn btn-primary btn-sm" type="button" variant="contained" color="primary" onClick={(e) => this.start(e)}>Start</button>
                        </div>
                    </td> 
                    : 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].startTime)}</td>
                    }

                    {
                    this.state.notes[index].endTime === 0 ? 
                    <td style={{ textAlign: "center" }}>
                        <div style={{ width: "100%" }}> 
                            <button name={index} className="btn btn-primary btn-sm" type="button" variant="contained" color="primary" onClick={(e) => this.end(e)}>End</button> 
                        </div>
                    </td> 
                    : 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].endTime)}</td>
                    }

                    {
                    this.state.notes[index].endTime !== 0 ? 
                    <td style={{ textAlign: "center" }}>{this.getFormatTime(this.state.notes[index].endTime - this.state.notes[index].startTime)}</td> 
                    : 
                    <td style={{ textAlign: "center" }}>--:--:--</td>
                    }

                </tr>
            )
        })

        return (
            <div className="container">
                <div className="scores-block1 clearfix">
                    <div className="bg-white shadow-sm Assessment_Results">
                        <div style={{ textAlign: "center" }}>
                                <div className="form-group row" >
                                    <label className="col-sm-3 col-form-label">Todo :  </label>
                                    <div className="col-sm-6">
                                        <input className="form-control" type="text" name="todo" placeholder="Add Todo" onChange={(e) => { this.handleInput(e) }} value={this.state.todo} />
                                    </div>
                                </div>
                                <div className="form-group row">
                                    <label className="col-sm-3 col-form-label">Estimated Time (minutes) : </label>
                                    <div className="col-sm-6">
                                        <input className="form-control" type="number" name="time" placeholder="Estimated time" onChange={(e) => { this.handleInput(e) }} value={this.state.time} />
                                    </div>
                                </div>
                                {this.state.todo.trim() === '' || this.state.time.trim() === '' ?
                                    <button title="Please add todo and time" disabled className="btn btn-lg" type="button" variant="contained" >
                                        Submit
                                    </button>
                                    : <button onClick={this.submitNote} className="btn btn-primary btn-lg" type="button" variant="contained" color="primary">
                                        Submit
                                    </button>
                                }
                        </div>
                        {this.state.notes.length !== 0 && <table className="table" style={{ marginTop: "2rem" }}>
                            <thead >
                                <tr >
                                    <th> <span className="icon-question-circle1" style={{ marginRight: "2px" }} />ToDo</th>
                                    <th> <span className="icon-clock" style={{ marginRight: "2px" }} />Estimated Time(minutes)</th>
                                    <th> <span className="icon-clock" style={{ marginRight: "2px" }} />Start Time(HH:MM:SS)</th>
                                    <th> <span className="icon-clock" style={{ marginRight: "2px" }} />End Time(HH:MM:SS)</th>
                                    <th> <span className="icon-clock" style={{ marginRight: "2px" }} />Total Time(HH:MM:SS)</th>
                                </tr>
                            </thead>
                            <tbody >
                                {todo}
                            </tbody>
                        </table>}
                    </div>
                </div>
            </div>
        );
    }
}

export default ToDoList;